@charset "UTF-8";
@import "variables";
@import "../../../sass/func";
$pl: pxToRem(32px);
$pr: pxToRem(20px);

.content-hc {
  // margin: pxToRem(20px) 0 pxToRem(88px);
  .box-50 {
    margin: pxToRem(20px);

    font-size: 0;
    white-space: nowrap;

     > div,
     > a {
        position: relative;
      display: inline-block;

      margin: 0 pxToRem(10px);
      border-radius: pxToRem(10px);
      width: pxToRem(336px);
      height: pxToRem(160px);

      font-size: pxToRem(30px);

      color: #222;
      background-color: #fff;
      padding: pxToRem(46px) 0 0 pxToRem(128px);
      box-sizing: border-box;
      box-shadow: 0 0 pxToRem(18px) pxToRem(2px) rgba(0, 0, 0, .06);
      i {
        position: absolute;
        top: 50%;
        left: pxToRem(35px);
        transform: translate(0, -50%);
      }
      span {
        display: block;

        font-size: pxToRem(24px);

        color: #999;
        padding-top: pxToRem(16px);
      }
    }
  }

  h3 {
    padding: 0 $pr 0 $pl;
    height: pxToRem(100px);

    font-size: pxToRem(32px);
    font-weight: bold;
    line-height: pxToRem(100px);
  }
  @at-root .list-ques {
    padding-left: $pl;

    .item {
      position: relative;

      color: #333;
      &:after {
        @include border(bottom, #f1f1f1);
      }
      &.open {
        .arrow {
          transform: translate(0, -50%) rotate(90deg);
        }
        .answer {
          display: block;
        }
      }
    }
    .ques {
      position: relative;

      box-sizing: border-box;
      padding: pxToRem(36px) pxToRem(20px+38px) pxToRem(36px) 0;

      font-size: pxToRem(30px);
      line-height: pxToRem(42px);
    }
    .answer {
      display: none;

      padding: 0 $pr pxToRem(30px) 0;

      font-size: pxToRem(30px);
      line-height: pxToRem(24px * 2);
      text-align: justify;

      color: #999;
    }
  }
}
